.container{
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 12px;
    user-select: none;
}

.container input{
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkmark{
    position: absolute;
    top: 10px;
    left: -30px;
    height: 25px;
    width: 25px;
    border-radius: 7px;
    background: $light;
}

.container:hover input ~ .checkmark{
    background: $oncheckedColor;
}

.container input:checked ~ .checkmark{
    background: $checkedColor;
}

.container:hover input:checked ~ .checkmark{
    background: lighten(grey, 30);
}

.container input:checked ~ .checkmark:after{
    display: block;
}

.container .checkmark:after{
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    @include bordered($light, $width: 3px);
    border-width: 0 3px 3px 0;
    transform: rotate(45deg);
}

.checkmark:after{
    content: "";
    position: absolute;
    display: none;
}